import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App.vue'
import { BootstrapVue, IconsPlugin } from 'bootstrap-vue'
import VueFreezeframe from 'vue-freezeframe';
import VueFreezeGif from 'vue-freeze-gif'
import axios from 'axios'
import VueAxios from 'vue-axios'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

import Overview from './components/Overview.vue'
import CreatePlan from './components/CreatePlan.vue'
import VueSocketIO from 'vue-socket.io'

Vue.config.productionTip = false

// Install BootstrapVue
Vue.use(BootstrapVue)
// Optionally install the BootstrapVue icon components plugin
Vue.use(IconsPlugin)
Vue.component('freeze', VueFreezeGif)
Vue.use(VueFreezeframe);
Vue.use(VueAxios, axios)
Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
  { path: '/', name: 'home', component: Overview },
  { path: '/create', name: 'create', component: CreatePlan }
  ]
  })

Vue.use(new VueSocketIO({
  debug: true,
  connection: `http://${window.location.host}/`,
  vuex: {
      actionPrefix: 'SOCKET_',
      mutationPrefix: 'SOCKET_'
  }
}))

new Vue({
  render: h => h(App),
  router,
}).$mount('#app')
