<template>
  <b-container>
    <b-form :action="url" method="POST">
      <b-form-group label-for="task" label="Task:">
        <b-form-input type="text" :state="validateTask" id="task" name="Task" v-model="taskname" placeholder="Name of the Task"></b-form-input>
        <b-form-invalid-feedback :state="validateTask">
          Taskname is to short or already exists for the selected BaseModel!
        </b-form-invalid-feedback>
      </b-form-group>

      <b-form-group label-for="case" label="Case">
        <b-form-select id="case" name="Case" v-model="selectedCase" :options="cases"></b-form-select>
      </b-form-group>
      <b-form-group label-for="data" label="Data">
        <b-form-select id="data" name="Data" :options="availabeData"></b-form-select>
      </b-form-group>
          <b-form-group label-for="version" label="BaseModel">
            <b-form-select id="version" name="ModelVersion" v-model="base" :options="models"></b-form-select>
          </b-form-group>
          <b-form-group label-for="maxClients" label="MaxClients">
            <b-form-input type="number" min="2" max="100" id="maxClients" name="MaxClients" placeholder="2-100" ></b-form-input>
          </b-form-group>
          <b-form-group label-for="date" label="Date">
            <b-form-datepicker type="date" id="date" v-model="date" :value="date" @change="updateTime()"></b-form-datepicker>
          </b-form-group>
          <b-form-group label-for="time" label="Time">
            <b-form-timepicker type="time" id="time" v-model="time" :value="time" locale="de" now-button @change="updateTime()"></b-form-timepicker>
          </b-form-group>
          <b-form-group class="hidden" label-for="utime" :value="unixtime" label="unixtime">
            <b-form-input type="text" id="utime" :readonly="true" v-model="unixtime" name="Time"></b-form-input>
          </b-form-group>
        <b-button  type="submit" variant="primary">Submit</b-button>
    </b-form>
  </b-container>
</template>

<script>

export default {
  name: 'CreatePlan',
  data: function () {
    return {
      date: "",
      time: "",
      url: "",
      taskname:"",
      base:"",
      unixtime: 0,
      selectedCase: "",
      data: {},
      cases: ['Coco'],
      models:[]
    }
  },
  mounted () {
    // Set default Date and Time
    this.url = `http://${window.location.host}/federatedPlan`
    let datetime = new Date()
    let date = datetime.toISOString().substr(0, 10)
    let time = datetime.toISOString().substr(11, 5)
    console.log("Date ", date)
    console.log("Time ", time)
    document.getElementById("date").value = date
    document.getElementById("time").value = time
    this.axios
      .get(`http://${window.location.host}/models`)
      .then(response => {
        console.log(response.data)
        this.models = []
        response.data.forEach(model =>{
          console.log(model)
          var name = model.name.split('.')[0]
          this.models.push(name)
        })
      })
    this.axios
      .get(`http://${window.location.host}/cases`)
      .then(response => {
        console.log(response.data)
        this.cases = []
        this.data = {}
        response.data.forEach(c => {
          this.cases.push(c.name)
          this.data[c.name] = c.data
        })
      })
  },
  methods:{
    updateTime: function (){
        console.log(this.date, this.time)
    }
  },
  computed:{
    availabeData (){
      return this.data[this.selectedCase] || ['Images']
    },
    validateTask(){
      if (`${this.taskname}`.length == 0){
        return null
      }
      return  !(this.models.includes(`${this.base}_${this.taskname}`)) &&  `${this.taskname}`.length > 1
    }

  },
  watch:{
    date: function (){
      console.log("Update " + this.date+"T"+this.time+":00.000Z")
      this.unixtime = new Date(this.date+"T"+this.time+".000Z").getTime()/100
    },
    time: function (){
      console.log("Update "+ this.date+"T"+this.time+":00.000Z")
      this.unixtime = new Date(this.date+"T"+this.time+".000Z").getTime()/100
    },
    taskname: function(){

    }

  }
}
</script>

