<template>
  <b-container fluid >
    <b-row class="diagram-row">
      <b-col md="6" offset-md="1" class="">
        <b-row v-bind:class="{ colour: isActive }">
            <h5>Step: {{id}}: {{ name }}</h5>
        </b-row>
        <b-row>
            <vue-freezeframe ref="freeze" class="animation" v-bind:class="{ inactive: !isActive }" :src="img"  />
        </b-row>
        <b-row v-if="buttonMsg && isNext">
            <b-button @click="buttonAction">{{buttonMsg}}</b-button>
        </b-row>
        <b-row v-if="msg">
            {{msg}}
        </b-row>
        <b-row v-if="data.length > 0">
            <b-table striped hover :items="data" :fields="keys"></b-table>
        </b-row>

      </b-col>
    </b-row>
  </b-container>
</template>

<script>
export default {
  name: 'Step',
  props: {
    id: Number,
    name: String,
    img: String,
    data: Array,
    keys: Array,
    step: Number,
    msg: String,
    buttonMsg: String,
    buttonAction: Function,
  },
  computed: {
    isActive: function() {
        return this.step == this.id
    },
    isNext: function() {
        return this.step + 1 == this.id
    }
  },
  mounted (){
      this.toogleAnimation()
  },
  methods: {
      toogleAnimation(){
        console.log("Animation ", this.id, ":", this.isActive)
        if (this.isActive){
            this.$refs.freeze.start();
        }else{
            this.$refs.freeze.stop();
        }
      }
  },
  watch: {
    isActive() {
        this.$refs.freeze.toggle()
        //this.toogleAnimation()
    }
  }
}
</script>

