ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

.diagram-row {
  height: 30vh;
}
.animation{
    max-height:100%;
    width:15vw;
}
.inactive {
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
    animation-play-state: paused;
}

.colour {
    Color: blue;
}
</style>
