<template>
  <b-container fluid class=" text-center small">
    <b-row class="mb-4 diagram-row-20">
      <b-col md="6" offset-md="4" class=""><Step :id="4" name="Aggregation" info="https://blog.bindwise.com/why-online-seller-have-to-get-rid-of-manual-inventory-management/" img="./assets/invenotry-grow.gif" :step="globalStep" :msg="aggregationInfo" :data="results" :keys="['metric', 'old', 'new']" :buttonAction="aggregateButton" :buttonMsg="aggregationButtonText" /></b-col>
    </b-row>
    <b-row class="mb-3 diagram-row-30">
      <b-col md="4" class="p-3 ">
        <Step :id="1" name="GlobalModel" info="https://4.bp.blogspot.com/-hLwbUkQIqpo/XO3IpFbdg4I/AAAAAAAzHoE/capc2dK0K4o1MjoJIxaQCepvL-fNaGh0ACLcBGAs/s1600/AW3884646_00.gif" img="./assets/ai.gif" :step="globalStep" :data="modelData" :keys="[ 'name', 'mAP', 'ar', 'loss']" :buttonAction="initButton" buttonMsg="Load Models" />

        </b-col>
      <b-col md="4" class="ml-auto p-8 "><Step :id="3" name="Training" info="https://albert.ai/wp-content/uploads/2016/09/blinkingBrain.gif" img="./assets/train.gif" :step="globalStep" :data="meta" :keys="['id', 'step', 'loss', 'acc']"/></b-col>
    </b-row>

     <b-row class="diagram-row-20">
      <b-col md="7" offset-md="4" class="">
        <b-row>
          <b-col md="4">
            <Step :id="2" name="Plan" info="https://cdn.lowgif.com/medium/d5e6f5cb080df421-clock-sticker-for-ios-android-giphy.gif" img="./assets/watch.gif" v-bind:step="globalStep" :data="[]" :buttonAction="createButton" buttonMsg="Create Plan" />
          </b-col>
          <b-col md="4" class="ml-n1">
            <table class="table">
              <tbody>
              <tr v-for="(value, name) in task" v-bind:key="name" >
                <th scope="row">{{name}}</th>
                <td v-if="name.includes('Time')" >{{format(value)}}</td>
                <td v-else >{{value}}</td>
              </tr>
            </tbody>
          </table>
          </b-col>
        </b-row>
      </b-col>

    </b-row>
  </b-container>
</template>

<script>
import Step from './Step.vue'
export default {
  name: 'Overview',
  components: {
    Step
  },
  props: {
    msg: String
  },
  data: function () {
    return {
      globalStep: 0,
      task: {},
      uploadLink: `http://${window.location.host}/`,
      taskKeys: [ 'Time', 'Task', 'ModelVersion', 'Data'],
      meta: [],
      modelData: [],
      results: [],
      trainInterval: null,
      aggregationInfo: null
    }
  },
  sockets: {
    connect: function () {
        console.log('socket connected')

    },
    aggregation: function (data) {
        console.log(data)
        this.aggregationInfo = data.msg
        if (data.status == 2){
          // Load eval results
          this.axios
          .get(`http://${window.location.host}/eval`)
          .then(response => {
            let data = response.data
            this.results = data
          })
        }

    },
    status: function(data){
      this.globalStep = data.Step
      let keys = ['nextTime', 'endTime', 'RegisteredClients', 'AcceptedClients', 'CompletedTasks']
      if (data.ActiveTask) {
        keys.forEach(key => {
          console.log(data[key] )
          if (data[key] !== 0){
            this.$set(this.task, key, data[key])
          }
        })
      }
    }
  },
  mounted () {
    let self = this
    self.axios
    .get(`http://${window.location.host}/status`)
    .then(response => {
      if (response.data.Step > 1){
        let s
        for(s=1; s<=response.data.Step; s++){
          this.updateData(s)
        }
      }

    })
  },
  computed: {
    aggregationButtonText(){
      return `Aggregate ${this.task.CompletedTasks || 0} clients`
    }

  },
  methods: {
    format: function(value){
      let date = new Date(value * 1000)
      return `${date.getHours()}:${date.getMinutes()}`
    },
    updateData: function(step){
      const self = this
      clearInterval(self.trainInterval)
      switch(step){
        case 0: {
          // Reset UI
          this.meta= []
          this.modelData= []
          this.results= []
          this.task = {}
          break
        }
        case 1: {

          this.axios
            .get(`http://${window.location.host}/models`)
            .then(response => {
              let data = response.data
              self.modelData = []
              data.forEach(element =>{
                let model = {}
                  Object.entries(element).forEach(pair =>{
                    let data = pair[1]
                    console.log(data, Number(data) && !Number.isInteger(data))
                    if (Number(data) && !Number.isInteger(data)){
                      data = Number(data).toFixed(3)
                    }
                    model[pair[0]] = data
                  })
                self.modelData.push(model)
                self.Step = 1
              })

            })
          break
        }
        case 2: {
          this.axios
          .get(`http://${window.location.host}/task/0`)
          .then(response => {
            let data = response.data
            console.log(Object.entries(data))
            Object.entries(data).forEach(pair =>{
              console.log(pair[0])
              if (this.taskKeys.includes(pair[0])){

                this.$set(this.task, pair[0], pair[1])
              }
            })
          })
          break;
        }
        case 3: {
          self.trainInterval = setInterval(function(){
            self.axios
            .get(`http://${window.location.host}/meta`)
            .then(response => {
                console.log(response.data)
                self.meta = []
                response.data.forEach(element => {
                  let client = {}
                  Object.entries(element).forEach(pair =>{
                    let data = pair[1]
                    if (Number(data) && !Number.isInteger(data)){
                      data = Number(data).toFixed(3)
                    }
                    client[pair[0]] = data
                  })
                  self.meta.push(client)
                })
            })
          }, 5000);
          break;
        }
      }
    },
    initButton: function(){
      this.globalStep = 1
    },
    createButton: function(){
      this.$router.push({ path: 'create' })
    },
    aggregateButton: function(){
      this.axios
      .get(`http://${window.location.host}/aggregate`)
    }
  },
  watch: {
    globalStep: function() {
        this.updateData(this.globalStep)
      }
  }
}
</script>

